@import 'colors';
@import 'mixins';

.site-dev-wrapper {
    @include flex(row, center, center);
}

.site-dev {
    @include flex(column, center, center);
    padding-top: 63px;
    padding-bottom: 93px;

    >span {
        font-size: 34px;
        font-weight: 400;
    }

    &-slider {
        padding-top: 28px;
        width: 100%;
        overflow: hidden;

        .slick {
            &-track {
                @include flex(row, stretch, center);
            }

            &-slide {
                @include flex(row, center, center);
            }

            &-dots {
                position: unset;
                padding-top: 42px;

                li {
                    width: 16px;
                    height: 16px;

                    button::before {
                        font-size: 12px;
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }

    &-element {
        @include flex(column, center, center);

        .course-img {
            @include flex(row, center, center);
        }
    }
}



.courses-info {
    @include flex(column, center, center);
    max-width: 350px;
    width: 100%;
}

.course {
    &-name {
        padding: 18px 0;
        font-size: 23px;
        font-weight: 600;
        color: $gray;
        border-bottom: $blue 1px solid;
        text-align: center;
    }

    &-desc {
        padding: 0 4px;
        padding-top: 28px;
        text-align: center;
        color: $gray;
        font-size: 15.2px;
        line-height: 1.62;
    }
}



@include adaptive(1100px) {
    .course-name {
        font-size: 15px;
        width: 100%;
    }
}

@include adaptive(851px) {
    .courses-info {
        padding: 0 14px;
    }
}


@include adaptive(500px) {
    .site-dev {
        padding-top: 61px;
        padding-bottom: 41px;

        >span {
            font-size: 24px;
        }
    }

    .course {
        &-name {
            font-size: 20px;
            padding-bottom: 6px;
            line-height: 1.2;
        }

        &-desc {
            line-height: 1.45;
            padding: 0 14px;
            padding-top: 19px;
            font-weight: lighter;
            font-size: 14px;
            transform: scaleY(1.1);
            padding-bottom: 4px;
        }
    }
}